import React, {Component} from 'react';

class StravaTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            activities: [],
            tableHead: [
                'Position',
                'Name',
                'No. Runs',
                'Run Distance',
                'No. Cycles',
                'Cycle Distance',
                'Total Complete'
            ],
            tableHeadSecond: [
                'Date',
                'Activity',
                'Distance',
                'Activity Time',
                'Average Speed',
            ],
            user: "",
        }
    }

    getHeader(headers) {
        // const headers = this.state.tableHead;
        return headers.map((header, i) => {
            return <th key={i} style={{padding: "5px 10px", borderBottom: "1px solid black"}}>{header}</th>
        })
    }

    setUser(athlete) {
        this.setState({
            ...this.state,
            user: athlete,
        });
    }

    getRowsData(row, i) {
        const position = i + 1;
        const name = row.name;
        const runNo = row.runQuantity;
        const runDistance = row.runDistance;
        const cycleNo = row.bikeQuantity;
        const cycleDistance = row.bikeDistance;
        const percentage = row.totalPercent;

        return (
            <tr onClick={() => this.setUser(name)}>
                <td key={i} style={{padding: "5px 0", borderBottom: "1px solid black"}}>{position}</td>
                <td key={i} style={{padding: "5px 0", borderBottom: "1px solid black"}}>{name}</td>
                <td key={i} style={{padding: "5px 0", borderBottom: "1px solid black"}}>{runNo}</td>
                <td key={i} style={{padding: "5px 0", borderBottom: "1px solid black"}}>{runDistance} km</td>
                <td key={i} style={{padding: "5px 0", borderBottom: "1px solid black"}}>{cycleNo}</td>
                <td key={i} style={{padding: "5px 0", borderBottom: "1px solid black"}}>{cycleDistance} km</td>
                <td key={i} style={{padding: "5px 0", borderBottom: "1px solid black"}}>{percentage.toFixed(2)}%</td>
            </tr>
        )
    }

    sepMethod() {
        return (
            <tr>
                <td key={2000}>-----</td>
                <td key={2000}>-----</td>
                <td key={2000}>---------</td>
                <td key={2000}>--------</td>
                <td key={2000}>--------</td>
            </tr>
        );
    }


    getDetailRowsData(row, index) {
        const allRuns = row.allRuns ? row.allRuns.map((r, i) => {
            const dist = r.distance / 1000;
            const time = r.moving_time / 60;
            const distance = (r.distance / 1000).toFixed(2)
            const movingTime = (r.moving_time / 60).toFixed(0);
            const averageSpeed = dist / (time/60);
            const oneKM = (1 / averageSpeed) * 60;
            const floor = Math.floor(oneKM);
            const decimal = (oneKM - floor) * 0.60;
            const km = (floor + decimal).toFixed(2);
            const day = r.start_date.substr(8,2);
            const month = r.start_date.substr(5,2);
            return (
            <tr>
                <td key={index * i + 5}>{day}/{month}</td>
                <td key={index * i}>Run</td>
                <td key={index * i + 1}>{distance} km</td>
                <td key={index * i + 2}>{movingTime} mins</td>
                <td key={index * i + 3}>{km} /km</td>
            </tr>
            );
        }) : [];

        const allCycles = row.allCycles ? row.allCycles.map((r, i) => {
            const distance = (r.distance / 1000).toFixed(2)
            const movingTime = (r.moving_time / 60).toFixed(0);
            const averageSpeed = (distance / (movingTime/60)).toFixed(1);
            const day = r.start_date.substr(8,2);
            const month = r.start_date.substr(5,2);
            return (
                <tr>
                    <td key={index * i + 5}>{day}/{month}</td>
                    <td key={index * i}>Cycle</td>
                    <td key={index * i + 1}>{distance} km</td>
                    <td key={index * i + 2}>{movingTime} mins</td>
                    <td key={index * i + 3}>{averageSpeed} km/h</td>
                </tr>
            );
        }) : [];

        const separator = this.sepMethod();

        let activities = allRuns;

        if ((allRuns.length > 0) && (allCycles.length > 0)) {
            activities = activities.concat(separator);
        }

        const allActivities = activities.concat(allCycles);

        return allActivities;
    }

    extraSection(rows) {
        const user = this.state.user;

        const userRows = rows.map(row => {
            if (row.name === user) {
                return row;
            } else {
                return [];
            }
        })

        if (user === "") {
            return <br />;
        } else {
            return (
                <table style={{marginLeft: "auto", marginRight: "auto", marginTop: "20px", border: "1px solid black"}}>
                    <thead>
                    <tr>{this.getHeader(this.state.tableHeadSecond)}</tr>
                    </thead>
                    <tbody>
                    {userRows.map((row, i) => {
                        return this.getDetailRowsData(row, i)
                    })}
                    </tbody>
                </table>
            );
        }
    }

    render() {
        const { allRows } = this.props;

        const sortedRows = allRows.length > 0 ? allRows.sort((a,b) => a.totalPercent > b.totalPercent ? -1 : 1) : [];

        return (
            <div>
                <table style={{marginLeft: "auto", marginRight: "auto", marginTop: "20px", border: "1px solid black"}}>
                    <thead>
                        <tr>{this.getHeader(this.state.tableHead)}</tr>
                    </thead>
                    <tbody>
                        {sortedRows.map((row, i) => {
                            return this.getRowsData(row, i)
                        })}
                    </tbody>
                </table>

                {this.extraSection(sortedRows)}

                {/*<br />*/}
                {/*<br />*/}
                {/*<table style={{marginLeft: "auto", marginRight: "auto", marginTop: "20px", border: "1px solid black"}}>*/}
                {/*    <thead>*/}
                {/*        <tr>{this.getHeader(this.state.tableHeadSecond)}</tr>*/}
                {/*    </thead>*/}
                {/*    <tbody>*/}
                {/*        {sortedRows.map((row, i) => {*/}
                {/*            return this.getDetailRowsData(row, i)*/}
                {/*        })}*/}
                {/*    </tbody>*/}
                {/*</table>*/}
            </div>
        )
    }
}

export default StravaTable;
